<!-- eslint-disable -->
<template>
  <div class="product__counter form__counter">
    <button
      type="button"
      aria-label="Убрать один товар"
      @click.prevent="deleteButton()">
      <svg
        width="10"
        height="10"
        fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input
    type="text"
    :value="amount"
    @input="$emit('update:amount', $event.target.value)"
    name="count">

    <button
      type="button"
      aria-label="Добавить один товар"
      @click.prevent="addButton()">
      <svg
        width="10"
        height="10"
        fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  data() {
    return {
    };
  },
  props: ['amount'],
  methods: {
    deleteButton() {
      if (this.amount > 1) {
        this.$emit('update:amount', this.amount - 1);
      }
    },
    addButton() {
      this.$emit('update:amount', this.amount + 1);
    },
  },
};
</script>
